.session_root_container {
    /*background: linear-gradient(to right, #BD3F32, #CB356B);*/
    background: #f0584d;
    border-radius: 1vw;
    padding: 2.5vw 4vw;
    margin: 1% 1%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: Arial, serif;
}
.session_type_container {
    display: flex;
    gap: 1.3vw;
}
.session-item {
    white-space: nowrap;
    background: transparent;
    border: none;
    border-radius: .35vw;
    color: white;
    font-size: 17px;
    width: 110px;
    height: 50px;
}
.session-item:hover {
    background-color: pink;
    cursor: pointer;
}
@media (max-width: 400px) {
    .session-item {
        border-radius: 1vw;
    }
}
.session-time {
    font-size: 90px;
    margin: 10%;
}
#session-start {
    font-size: 30px;
    background: white;
    border: none;
    border-radius: .3vw;
    width: 180px;
    height: 55px;
    box-shadow: 0 9px #999;
    cursor: pointer;
}
#session-start:hover {
    background-color: pink;
}
@media (max-width: 400px) {
    #session-start {
        margin-bottom: 20px;
        border-radius: 2vw;
    }
}
.session-selected {
    background-color: pink;
    color: mediumslateblue;
    font-weight: bold;
}
.session-start-on {
    box-shadow: 0 0px #666 !important;
    transform: translateY(9px);
}
